document.addEventListener("DOMContentLoaded",function(){
    iniciarApp();
});

function iniciarApp(){
    crearGaleria();
    smothScrooll();
    navScrool();
}

function navScrool(){
    const nav = document.querySelector(".header");
    const sobreFestival = document.querySelector(".sobre-festival");
    const body = document.querySelector("body");
    const lineupColor = document.querySelectorAll(".navegacion-principal a")[0];
    const galeriaColor = document.querySelectorAll(".navegacion-principal a")[1];
    const boletosColor = document.querySelectorAll(".navegacion-principal a")[2];
    

    window.addEventListener("scroll",()=>{
        console.log(sobreFestival.getBoundingClientRect());
        console.log(boletosColor)
        if (sobreFestival.getBoundingClientRect().y <= -50) {
            nav.classList.add("header-fijo")
            body.classList.add("scroll-body");
        } else{
            nav.classList.remove("header-fijo")
            body.classList.remove("scroll-body");
            lineupColor.classList.remove("nav-color");
        }
        if (sobreFestival.getBoundingClientRect().y <= -250 ) {     
            lineupColor.classList.add("nav-color");
            galeriaColor.classList.remove("nav-color");
        } 
        if (sobreFestival.getBoundingClientRect().y <= -2020) {
            galeriaColor.classList.add("nav-color");
            lineupColor.classList.remove("nav-color");
            boletosColor.classList.remove("nav-color");
        } 
        if (sobreFestival.getBoundingClientRect().y <= -2350) {
            boletosColor.classList.add("nav-color");
            galeriaColor.classList.remove("nav-color");
        } 
    })
}

function smothScrooll(){
    const botones = document.querySelectorAll(".navegacion-principal a")
    botones.forEach(e =>{
        e.addEventListener("click",(id)=>{
            id.preventDefault();

            const seccionScrool = id.target.attributes.href.value;
            const seccion = document.querySelector(seccionScrool);
            seccion.scrollIntoView({behavior: "smooth"});
        })
    })
}

function crearGaleria(){
    const galeria = document.querySelector(".galeria-imagenes");
    for (let i = 1; i <= 12; i++) {
        const imagenes = document.createElement("picture");
        imagenes.innerHTML = `
        <source srcset="./build/img/thumb/${i}.avif" type="image/avif">
        <source srcset="./build/img/thumb/${i}.webp" type="image/webp">
        <img width="300" height="200" loading="lazy" src="./build/img/thumb/${i}.jpg" alt="imagen de galeria">
        `;
        imagenes.onclick = function(){
            mostrarImagen(i);
        }
        galeria.appendChild(imagenes);
    }
}

function mostrarImagen(id){
    const imagenes = document.createElement("picture");
        imagenes.innerHTML = `
        <source srcset="./build/img/grande/${id}.avif" type="image/avif">
        <source srcset="./build/img/grande/${id}.webp" type="image/webp">
        <img width="300" height="200" loading="lazy" src="./build/img/grande/${id}.jpg" alt="imagen de galeria">
        `;

    //Crear imagen
    const overley = document.createElement("div")
    overley.appendChild(imagenes);
    overley.onclick = function(){
        body.classList.remove("fijar-body")
        overley.remove();
    }
    overley.classList.add("overlay")

    //crear boton cerrar
    const cerrarModal = document.createElement("p");
    cerrarModal.classList.add("btn-cerrar")
    cerrarModal.textContent = "X";
    cerrarModal.onclick = function(){
        body.classList.remove("fijar-body")
        overley.remove();
    }
    overley.appendChild(cerrarModal);

    //agregar overlay al body
    const body = document.querySelector("body")
    body.classList.add("fijar-body")
    body.appendChild(overley)
}

